const providerIcons: Record<SocialProvider, React.FC<{ className?: string }>> = {
  google: GoogleIcon,
  github: GitHubIcon,
  twitter: TwitterIcon,
  facebook: FacebookIcon,
  apple: AppleIcon,
  discord: DiscordIcon,
  linkedin: LinkedInIcon,
  spotify: SpotifyIcon,
};

const providerNames: Record<SocialProvider, string> = {
  google: "Google",
  github: "GitHub",
  twitter: "X",
  facebook: "Facebook",
  apple: "Apple",
  discord: "Discord",
  linkedin: "LinkedIn",
  spotify: "Spotify",
};

export default function ButtonSocial({
  provider,
  variant = "default",
  size = "md",
  children,
  className = "",
  ...props
}: ButtonSocialProps) {
  const baseStyles =
    "cursor-pointer inline-flex items-center justify-center font-medium rounded-2xl transition-all duration-200 focus:outline-none active:scale-95 disabled:opacity-50 disabled:pointer-events-none gap-2";

  const colors = brandColors[provider];
  const Icon = providerIcons[provider];
  const defaultText = `Continue with ${providerNames[provider]}`;

  const variants = {
    default: `${colors.bg} ${colors.hover} ${colors.text} border ${colors.border}`,
    outline: `bg-transparent border-2 ${colors.border} ${colors.text} hover:${colors.bg}`,
    icon: `${colors.bg} ${colors.hover} ${colors.text}`,
  };

  const sizes = {
    sm: variant === "icon" ? "p-2" : "px-4 py-2 text-sm",
    md: variant === "icon" ? "p-3" : "px-6 py-2.5 text-sm",
    lg: variant === "icon" ? "p-4" : "px-8 py-3 text-base",
  };

  const iconSizes = {
    sm: "w-4 h-4",
    md: "w-5 h-5",
    lg: "w-6 h-6",
  };

  return (
    <button
      className={`${baseStyles} ${variants[variant]} ${sizes[size]} ${className}`}
      {...props}
    >
      <Icon className={iconSizes[size]} />
      {variant !== "icon" && <span>{children || defaultText}</span>}
    </button>
  );
}
